import React, { Suspense } from "react";
// import IFrame from "react-iframe";
import { Container, Row, Col } from "react-bootstrap";
const IFrame = React.lazy(() => import("react-iframe"));
function KontakPage(props) {
  return (
    <Container>
      <Row>
        <Col xs={12} sm={12} md={8} className="pt-3 mx-auto">
          <Suspense
            fallback={<div className="container mx-auto">Loading...</div>}
          >
            <IFrame
              title="Pemesanan"
              url="https://docs.google.com/forms/d/e/1FAIpQLSehQVubzxQmQqeFBioGpKJFNZlE3XPapi59XKaLf4C_X3pGQQ/viewform?embedded=true"
              height="1200"
              width="100%"
              frameBorder="0"
              marginHeight="0"
              marginWidth="0"
              style={{ width: "100%" }}
            >
              Loadingâ€¦
            </IFrame>
          </Suspense>
        </Col>
      </Row>
    </Container>
  );
}

export default KontakPage;
